import React, { useState } from "react"
import styled from "styled-components"
import MainGrid from "../../shared/MainGrid"
import theme from "../../shared/theme"
import Button from "../Button"

interface Item {
  img: string
  alt: string
}
interface HpGrid {
  data: Array<Item>
}

const tabs = [{ heading: "Web development" }, { heading: "Web design" }]

const section = [{ heading: "web dev" }, { heading: "Web des" }]
const ProjectsGrid = ({ data }: HpGrid) => {
  const [category, setCategory] = useState(0)

  return (
    <MainGrid>
      <Inner>
        <Section>
          <Tabs id="tabs">
            {tabs &&
              tabs.map((item, key) => {
                return (
                  <Tab
                    onClick={() => {
                      setCategory(key)
                    }}
                    isActive={key === category}
                  >
                    {item.heading}
                  </Tab>
                )
              })}
          </Tabs>

          <p>{section[category].heading}</p>
        </Section>
        <Grid>
          {data &&
            data.map((item, key) => {
              return (
                <figure key={key}>
                  <img src={item.img} alt={item.alt} />
                </figure>
              )
            })}
        </Grid>
        <Button href="/projects" text="View more" marginTop="40px" />
      </Inner>
    </MainGrid>
  )
}

export default ProjectsGrid

const Inner = styled.section`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10rem;
`

const Grid = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  width: 100%;

  @media (min-width: 1024px) {
    grid-template-columns: repeat(3, 1fr);
  }
  @media (min-width: 1200px) {
    grid-template-columns: repeat(10, 1fr);
    grid-row-gap: 1rem;
    grid-column-gap: 1rem;
  }

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    transition: all 0.5s;
  }

  & figure:nth-of-type(1) {
    @media (min-width: 1200px) {
      grid-column: 1 / span 3;
      grid-row: 1 / span 3;
    }
  }

  & figure:nth-of-type(2) {
    @media (min-width: 1200px) {
      grid-column: 4 / span 2;
      grid-row: 5 / span 1;
    }
  }

  & figure:nth-of-type(3) {
    @media (min-width: 1200px) {
      grid-column: 1 / span 3;
      grid-row: 4 / span 2;
    }
  }

  & figure:nth-of-type(4) {
    @media (min-width: 1200px) {
      grid-column: 4 / span 2;
      grid-row: 1 / span 1;
    }
  }

  & figure:nth-of-type(5) {
    display: none;
    @media (min-width: 1024px) {
      display: block;
    }
    @media (min-width: 1200px) {
      grid-column: 6 / span 2;
      grid-row: 1 / span 1;
    }
  }

  & figure:nth-of-type(6) {
    @media (min-width: 1200px) {
      grid-column: 4 / span 4;
      grid-row: 2 / span 3;
    }
  }

  & figure:nth-of-type(7) {
    @media (min-width: 1200px) {
      grid-column: 6 / span 2;
      grid-row: 5 / span 1;
    }
  }

  & figure:nth-of-type(8) {
    @media (min-width: 1200px) {
      grid-column: 8 / span 4;
      grid-row: 1 / span 2;
    }
  }

  & figure:nth-of-type(9) {
    @media (min-width: 1200px) {
      grid-column: 8 / span 4;
      grid-row: 3 / span 3;
    }
  }
`
const Tabs = styled.div`
  display: flex;
  justify-content: flex-start;
  margin-bottom: 5rem;
`

const Section = styled.div`
  width: 100%;
  padding: 5rem 0;

  @media (min-width: 1024px) {
    padding: 10rem 0 15rem;
  }
`

interface ITab {
  isActive?: boolean
}
const Tab = styled.p<ITab>`
  width: 200px;
  padding-bottom: 23px;
  position: relative;
  &::after {
    position: absolute;
    content: " ";
    height: 2px;
    border-radius: 30px;
    width: 100%;
    bottom: -1px;
    left: -1px;
    background: ${theme.accent};
    transition: all ease-out 0.3s;
    margin: 0 auto;
    transform: scaleX(0);
    ${props => props.isActive && `transform: scaleX(1);`}
  }

  cursor: pointer;
  text-align: center;
  margin-right: 13px;

  @media (max-width: 1023px) {
    width: auto;
    flex-shrink: 0;
  }

  @media (max-width: 767px) {
    padding-bottom: 5px;
  }
`
